import React from "react";
import { useParams, useRouteMatch, Redirect, NavLink, Route, Switch } from 'react-router-dom'
import { translate } from './dictionary';

import '../css/style.scss';
import { createPartiallyEmittedExpression } from "typescript";

const Details = props => {

    const { loaded } = props;

    const DetailsComponent = (props) => {

        function buildDetails(array, id, category) {
            let detailsTitle ="";
            let detailsList = "";
            let properties = [];
            if (id !== undefined) {
              const key = category === "films" ? "title" : "name";
              let arrayTemp = array.props[category].filter( item => item[key] === id)[0];
              if (arrayTemp !== undefined) {
                  Object.entries(arrayTemp).forEach(([key]) => {
                      if (key !== "created" && key !== "edited" && key !== "url" && key !== "opening_crawl")
                      properties.push(key)
                  });
              }
              else 
                  return <Redirect to= "/" />   

              detailsTitle = translate(id);
              detailsList = properties.map( el =>
                  <div key={(el)} className="detailsWrap">
                      <div className="detailsName">{translate(el)}: </div>
                      <div className="detailsProp">{getDetails(arrayTemp[el],el, array)}</div>
                  </div>
              )
              return (
                  <div>
                      <p className="detailsTitle">{detailsTitle}</p>
                      {detailsList}
                  </div>
              )
            }
            else {
                return (
                    <div>
                        <p className="detailsTitle">Wybierz pozycję</p>
                    </div>
                )            
            }
        }
        const getDetails = (value, property, array) => {
            // console.log('property', property);
            // console.log('value', value);
            
            console.log('array:');
            console.log(array);

            // value: tylko wybrane
            // 0:"https://swapi.dev/api/people/1/"
            // 1:"https://swapi.dev/api/people/2/"
            // 2:"https://swapi.dev/api/people/3/"
            // 3:"https://swapi.dev/api/people/4/"
            //16:"https://swapi.dev/api/people/19/"
            //17:"https://swapi.dev/api/people/81/"

            // array.props["people"][i].name

            // if (property == "characters") {
            //     let key = property === "films" ? "title" : "name";
            //     console.log("@@@@");
            //     property = property === "characters" ? "people": property;
            //     array.props[property].forEach( el => {
            //         console.log('value:');
            //         console.log(value);
            //         console.log(value.length);
            //         value.forEach( el => {
            //             console.log(
                            
            //                 array.props[property].filter(element => element.url === el)[0][key]
                            
            //                 );
            //         })
            //         // let filteredValue = array.props[property].filter( element => element.url === value[0] )[0][key]; 
            //         // console.log(array.props[property].filter( element => element.url === value[0] ));
            //     })
            // }

            if (array.loaded.films && array.loaded.people && array.loaded.planets && array.loaded.species && array.loaded.starships && array.loaded.vehicles) {
                let key = property === "films" ? "title" : "name"
                property = property === "characters" ? "people": property;
                property = property === "residents" ? "people" : property;
                property = property === "pilots" ? "people" : property;
                property = property === "homeworld" ? "planets" : property;

                // console.log(property)

                if (value === null)
                    return "Brak";
                else if (typeof value == "number")
                    return value.toString();
                else if (typeof value === "object" && value.length === 0)
                    return "Brak";
                else if (typeof value[0] === "string" && value[0].indexOf("https://") !== -1) {
                    
                    let properties = [];
                    value.forEach( el => {
                        properties.push(array.props[property].filter( element => element.url === el )[0][key]);
                    });
                    let detailsList = "";
                    detailsList = properties.map( el => 
                        
                            <NavLink to={`/${property}/${ el }`}>{translate(el)}, </NavLink>
                            // <div>xxx</div>
                    )
                    return <span> {detailsList} </span>;
                         
                }
                else if (typeof value === "string" && value.indexOf("https://") !== -1) {
                    let filteredValue = array.props[property].filter( element => element.url === value )[0][key]; 
                    return filteredValue === "Brak danych" ? "Brak danych" : <NavLink to={`/${property}/${filteredValue}`}>{translate(filteredValue)}</NavLink>
                }
                else if (typeof value === "string" && value.indexOf("https://") === -1) {
                    return translate(value);
                }
                    
                else {
                    return translate(value);
                }
                    
            } 
        }

        const {id} = useParams();
        const {path} = useRouteMatch();
        const category = path.substring(1,path.indexOf("/",1));
        return <div className="detailsSection">{buildDetails(props, id, category)}</div>
    }

    return (
        <Switch>
            <Route path="/films" exact component={() => {
                return (
                    <DetailsComponent
                        array = {undefined}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/films/:id" component={() => {
                return (
                    <DetailsComponent
                        props = {props}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/people" exact component={() => {
                return (
                    <DetailsComponent
                        array = {undefined}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/people/:id" component={() => {
                return (
                    <DetailsComponent
                        props = {props}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/planets" exact component={() => {
                return (
                    <DetailsComponent
                        array = {undefined}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/planets/:id" component={() => {
                return (
                    <DetailsComponent
                        props = {props}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/species" exact component={() => {
                return (
                    <DetailsComponent
                        array = {undefined}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/species/:id" component={() => {
                return (
                    <DetailsComponent
                        props = {props}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/starships" exact component={() => {
                return (
                    <DetailsComponent
                        array = {undefined}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/starships/:id" component={() => {
                return (
                    <DetailsComponent
                        props = {props}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/vehicles" exact component={() => {
                return (
                    <DetailsComponent
                        array = {undefined}
                        loaded = {loaded}
                    />
                )
            }}/>
            <Route path="/vehicles/:id" component={() => {
                return (
                    <DetailsComponent
                        props = {props}
                        loaded = {loaded}
                    />
                )
            }}/>
        </Switch>
    )
}

export default Details